














































import { Component, Watch, Prop, Vue, Inject } from "vue-property-decorator";
import { State } from "vuex-class";

@Component
export default class NavBar extends Vue {
  // vuex 的状态
  @State(state => state.app.isFold) isFold!: boolean;

  // 注入重载
  @Inject() reload;

  handleOpen() {}

  handleClose() {}

  handleSelect() {
    //点击侧边栏页面重载
    this.reload();
  }
}
