import Vue from 'vue';
import VueRouter, { RouteConfig, RawLocation } from 'vue-router';
import Home from '../views//Home/index.vue';
import Login from '../views/Login/index.vue';
import { generateRoutes } from './permission';
import store from '@/store';

Vue.use(VueRouter);

/**
 * 重写路由跳转
 */
let routerPush = VueRouter.prototype.push;
VueRouter.prototype.push = function(location: RawLocation) {
	return (<any>routerPush.call(this, location)).catch((error: any) => error);
};
//公共路由，全部角色都具有的路由
const routes = [
	{
		path: '/login',
		name: 'login',
		component: Login,
		hidden: true
	},

	{
		path: '/',
		component: Home,
		redirect: '/dashboard',
		name: '',
		iconCls: 'el-icon-s-help',
		leaf: true,
		children: [
			{
				path: '/dashboard',
				name: '首页',
				component: () => import(/* webpackChunkName: "dashboard" */ '../views/Dashboard/index.vue')
			}
		]
	}
];

//根据权限进行的动态生成路由
const asyncRoutes = [
	{
		path: '/',
		component: Home,
		iconCls: 'el-icon-sgrid',
		leaf: true,
		children: [
			{
				name: '表格',
				path: '/admin',
				component: () => import('../views/AdminTable/index.vue'),
				meta: {
					roles: [ 'admin' ]
				}
			}
		]
	},
	{
		path: '/',
		component: Home,
		name: '',
		iconCls: 'el-icon-edit-outline',
		leaf: true,
		children: [
			{
				name: '富文本',
				path: '/tiny',
				component: () => import(/* webpackChunkName: "dashboard" */ '../views/Tinymce/index.vue'),

				meta: {
					roles: [ 'editor', 'admin' ]
				}
			}
		]
	}
];

const router = new VueRouter({
	mode: 'hash',
	base: process.env.BASE_URL,
	routes
});

let flag = true; //页面进行刷新
router.beforeEach((to, from, next) => {
	// debugger;
	//跳转到login 的时候 重置登录状态
	if (to.path === '/login') {
		sessionStorage.removeItem('name');
		sessionStorage.removeItem('pass');
		//全局重置路由
		store.dispatch('app/ResetRouter');
		flag = true;
	}

	let admin = <string>sessionStorage.getItem('name');
	//如果没有登录的情况下访问非登录页则 跳转到登录页
	if (!admin && to.path !== '/login') {
		next({ path: '/login' });
	} else {
		if (flag && to.path !== '/login') {
			flag = false;
			router['options'].routers = routes;
			let generateAsyncRoutes = generateRoutes(asyncRoutes, <string>sessionStorage.getItem('name')); //根据登录角色生成动态路由

			router.addRoutes(generateAsyncRoutes);
			router['options'].routes = router['options'].routes.concat(generateAsyncRoutes);
			// next();
			next(<any>{ ...to, replace: true }); // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
		} else {
			console.log(to, ':as');
			if (to.path !== '/login') {
				let newPath = {
					name: to.name,
					path: to.fullPath
				};
				store.dispatch('app/AddRouter', newPath);
			}
			next();
		}
	}
});

export default router;
